<template>
  <ul>
    <li  v-for="item in cert">
      <span class="name">{{item.key}}</span>
      <input :class="['input',isButtonReadonly?'Nshow':'Tshow']"  :readonly="isButtonReadonly" v-model="form[item.value]" />
    </li>
  </ul>
</template>
<style lang="scss" scoped>
  @import "certinfo.scss";
</style>
<script>
  export  default {
      props:['form','isButtonReadonly'],
      data(){
          return {
              cert:[
                {key:'',value:'certificatename',display:true},
                {key:'',value:'name',display:true},
                {key:'',value:'gender',display:true},
                {key:'',value:'cardid',display:true},
                {key:'',value:'birthdate',display:true},
                {key:'',value:'certificatenumber',display:true},
                {key:'',value:'approvalofdate',display:true},
                {key:'',value:'issuanceoftime'},
                {key:'',value:'issuanceagencies'},
              ]
          }
      }
  }
</script>



